<template>
    <div>
        <el-container style="height: 700px; border: 1px solid #eee">
            <el-header style="font-size:40px; background-color: rgb(238, 241, 246)">tlias </el-header>
            <el-container>
                <el-aside width="230px" style="border: 1px solid #eee">
                    <el-menu :default-openeds="['1', '3']">
                        <el-submenu index="1">
                            <template slot="title">
                                <i class="el-icon-message"></i>
                            </template>
                            <el-menu-item index="1-1">
                                <router-link to="/dept"></router-link>
                            </el-menu-item>
                            <el-menu-item index="1-2">
                                <router-link to="/emp"></router-link>
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>

                <el-main>
                    <!--  -->
                    <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                        <el-form-item label="">
                            <el-input v-model="searchForm.name" placeholder=""></el-input>
                        </el-form-item>

                        <el-form-item label="">
                            <el-select v-model="searchForm.gender" placeholder="">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="">
                            <!--  -->
                            <el-date-picker
                                v-model="searchForm.entrydate"
                                type="daterange"
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                            ></el-date-picker>
                        </el-form-item>

                        <el-form-item>
                            <el-button type="primary" @click="onSubmit"></el-button>
                        </el-form-item>
                    </el-form>

                    <!--  -->
                    <el-table :data="tableData" border>
                        <el-table-column prop="name" label="" width="180"></el-table-column>
                        <el-table-column label="" width="180">
                            <template slot-scope="scope">
                                <img :src="scope.row.image" width="100px" height="70px" />
                            </template>
                        </el-table-column>
                        <el-table-column label="" width="140">
                            <template slot-scope="scope">{{scope.row.gender == 1 ? '' : ''}}</template>
                        </el-table-column>
                        <el-table-column prop="job" label="" width="140"></el-table-column>
                        <el-table-column prop="entrydate" label="" width="180"></el-table-column>
                        <el-table-column prop="updatetime" label="" width="230"></el-table-column>
                        <el-table-column label="">
                            <el-button type="primary" size="mini"></el-button>
                            <el-button type="danger" size="mini"></el-button>
                        </el-table-column>
                    </el-table>

                    <br />
                    <br />

                    <!--  -->
                    <el-pagination
                        background
                        layout="sizes, prev, pager, next, jumper, total"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :total="1000"
                    ></el-pagination>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            tableData: [],
            searchForm: {
                name: "",
                gender: "",
                entrydate: []
            }
        };
    },

    methods: {
        onSubmit: function() {
            alert("");
        },
        handleSizeChange: function(val) {
            alert("" + val);
        },
        handleCurrentChange: function(val) {
            alert("" + val);
        }
    },
    mounted() {
        //
        axios
            .get("https://mock.apifox.cn/m1/3128855-0-default/emp/list")
            .then(result => {
                this.tableData = result.data.data;
            });
    }
};
</script>

